/* ==========================================================================
	Related News - Categories
============================================================================= */
.fw-component-related-news-categories {
  position: relative;
}
.fw-component-related-news-categories:before {
  width: calc(100% + 50px);
  height: 30px;
  display: block;
  position: absolute;
  left: -25px;
  top: 0;
  background: #fff;
  clip-path: ellipse(50% 20px at 50% 0%);
  content: "";
  transform: rotate(0.5deg) translateY(-6px);
}
@media (min-width:740px) {
  .fw-component-related-news-categories:before {
    clip-path: ellipse(50% 30px at 50% 0%);
  }
}
@media (min-width:980px) {
  .fw-component-related-news-categories:before {
    height: 50px;
    clip-path: ellipse(50% 50px at 50% 0%);
  }
}
@media (min-width:1220px) {
  .fw-component-related-news-categories:before {
    height: 70px;
    clip-path: ellipse(50% 70px at 50% 0%);
  }
}
.fw-component-related-news-categories:after {
  width: calc(100% + 50px);
  height: 30px;
  display: block;
  position: absolute;
  left: -25px;
  bottom: 0;
  background: #fff;
  clip-path: ellipse(50% 20px at 50% 100%);
  content: "";
  transform: rotate(-0.5deg) translateY(6px);
}
@media (min-width:740px) {
  .fw-component-related-news-categories:after {
    clip-path: ellipse(50% 30px at 50% 100%);
  }
}
@media (min-width:980px) {
  .fw-component-related-news-categories:after {
    height: 50px;
    clip-path: ellipse(50% 50px at 50% 100%);
  }
}
@media (min-width:1220px) {
  .fw-component-related-news-categories:after {
    height: 70px;
    clip-path: ellipse(50% 70px at 50% 100%);
  }
}

